@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar {
  width: 5px;
}
::-webkit-scrollbar:hover {
  width: 15px;
}
/* Track */
::-webkit-scrollbar-track {
  box-shadow:  inset 0 0 5px grey; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #c2bfcc36 ; 
  border-radius: 20px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #2700b33f; 
}
.chat {
    display: flex;
    flex-direction: column;
    flex: 1 1;
    width: 94.15vw;
    margin-top: 60px;
    background-color: whitesmoke;
  }
  .chat_header {
    font-family: 'Lobster', cursive;
    padding: 20px;
    display: flex;
    /* align-items: center; */
    border-bottom: 1px solid lightgray;
    background-color: #486EC4;
  }
  .chat_headerinfo {
    flex: 4 1;
    padding-left: 20px;
    width: 1%;
  }
  .chat_headerinfo > h3 {
    margin-bottom: 3px;
    font-weight: 500;
    color: white;
  }
  .chat_headerinfo > p {
    color: whitesmoke;
  }
  .chat_body {
    flex: 1 1;
    height: 85vh;
    background-image: url("https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png");
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow: scroll;
    scroll-behavior: smooth;
  }
  .chat_message {
    position: relative;
    font-size: 16px;
    padding: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background-color: #ffffff;
    border-radius: 10px;
    margin-bottom: 30px;
  }
  .chat_reciever {
    background-color: #dcf8c6;
    margin-left: auto;
  }
  .chat_timestamp {
    margin-left: 10px;
    font-size: xx-small;
  }
  .chat_name {
    position: absolute;
    top: -15px;
    font-weight: 800;
    font-size: xx-small;
  }
  .chat_footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
    border-top: 1px solid lightgray;
  }
  .chat_footer > form {
    flex: 1 1;
    display: flex;
  }
  .chat_footer > form > input {
    flex: 1 1;
    border-radius: 30px;
    padding: 10px;
    border: none;
    outline: 0;
  }
  .chat_footer > form > button {
    display: none;
  }
  .chat_footer > .MuiSvgIcon-root {
    padding: 10px;
    color: gray;
  }

body {
